
body{
  margin: 0 0 0 0;
  background-color:#414141;
}
.header{
  margin: 0%;
  padding: 0;
  width: 100%;
  height: 10vh;
  min-height: 100px;
  background-color: #93BF47 ;
  color:#414141 ;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  left:30px;
  background-color: transparent;
  height: 75px;
  width: 276px;
  position:relative;
}
.header nav {
  height: 100%;
  width: 45%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header nav button{
  color: #000000;
  background: #93BF47;
  border: 1px solid #93BF47;
  border-radius: 5px;
  padding: 10px 20px;
  font-family: "Roboto-Bold", Helvetica, Arial, serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
}
.header nav button:hover{
  color: #414141;
  background: #E6B06B;
  border: 1px solid #E6B06B;
}
.main-component{
  height: 75vh;
  margin: 0%;
  padding: 0%;
  background: #414141 ;
}
.footer{
  position:absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 15vh;
  min-height: 100px;
  background-color: #93BF47;
  color: #000000;
}
.footer h2{
  width: 100%;
  height: 100%;
  left:calc(50.6% - 30px);
  position:absolute;
  justify-content: center;
  align-items: center;
  font-family: "Roboto-Regular", Helvetica, Arial, serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
}
.mintic{
  position:absolute;
  bottom:15px;
  left:calc(39.16% - 50px);
  right:0;
  width:153px;
  height:70px;
}

#User{
    width: 100%;
    height: 100%;

    display: flex;
    position:relative;
    justify-content: space-around;  
    align-items: center;
}
#User h2{
    color: #E5E7E9;
    text-align: center;
    font-family: "Roboto-Bold", Helvetica, Arial, serif;
    font-size: 50px;
    font-style: normal;
    font-weight: 700;
    justify-content: center;
    align-items: center;
}
#User span{
    color: #E6B06B;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    justify-content: center;
    align-items: center;
}

.auth_user{
    margin: 0;
    padding: 0%;
    height: 100%;
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}
.container_auth_user {
    width: 40%;
    height: 60%;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.auth_user h2{
    font-size: 50px;
    color: #ffffff;
    text-align: center;
    font-family: "Roboto-Bold", Helvetica, Arial, serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
}
.auth_user form{
    width: 50%;
}
.auth_user input{
    height: 40px;
    width: 100%;

    box-sizing: border-box;
    padding: 10px 20px;
    margin: 5px 0;

    border: 1px solid #283747;
}
.auth_user button{
    width: 100%;
    height: 40px;

    color: #283747;
    background: #E6B06B;
    border: 1px solid #E6B06B;

    border-radius: 5px;
    padding: 10px 25px;
    margin: 5px 0;
}
.auth_user button:hover{
    color: #414141;
    background: #93BF47;
    border: 1px solid #93BF47;
}


#Inventory{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
#Inventory h2{
    font-size: 50px;
    color: #ffffff;
    text-align: center;
    font-family: "Roboto-Bold", Helvetica, Arial, serif;
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
}
#Inventory button{
    width: 100%;
    height: 40px;

    color: #242424;
    background: #E6B06B;
    border: 1px solid #E6B06B;

    border-radius: 5px;
    padding: 10px 25px;
    margin: 5px 0;
    font-weight: 700;
}
#Inventory button:hover{
    color: #414141;
    background: #93BF47;
    border: 1px solid #93BF47;
}

